import React from "react";
import PropTypes from "prop-types";
import { cartItemsCountSelector, cartTotalSelector } from "../selectors";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { Button } from "@mui/material";
import { removeAllCart } from "../cartSlice";

Bill.propTypes = {};

function Bill(props) {
  const cartTotal = useSelector(cartTotalSelector);
  const cartItemsCount = useSelector(cartItemsCountSelector);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleBuy = () => {
    alert(
      "Tnh nng mua hng ch tm dng li  mc xa sn phm khi gii hng. Thanks"
    );
    const action = removeAllCart();
    dispatch(action);
    navigate({
      pathname: "/products",
    });
  };
  return (
    <>
      <div className='flex justify-between'>
        <h1 className='font-semibold'>Tng tin:</h1>
        <div className='flex flex-col'>
          <h1 className='text-red-600 text-right'>
            {Intl.NumberFormat("vi-VN", {
              style: "currency",
              currency: "VND",
            }).format(cartTotal)}
          </h1>
          <h1 className='text-sm mb-5'>( bao gm VAT nu c)</h1>
        </div>
      </div>
      <Button
        onClick={handleBuy}
        className='w-full'
        variant='contained'
        type='submit'>
        Mua hng ({cartItemsCount})
      </Button>
    </>
  );
}

export default Bill;
